section.cash-flow {
  background-color: $primary;
  .cash-flow-content {
    width: 80%;
    margin: 0 auto;
    padding: 60px 0;
    .text-and-photo {
      display: flex;
      justify-content: space-evenly;
      align-items: flex-end;
      margin-bottom: 30px;
      .left {
        h3 {
          font-size: 2.5rem;
          color: $bg-color; }
        img {
          height: 220px; } }
      .right {
        img {
          height: 400px; } } }
    p {
      font-size: 1.3rem;
      color: $bg-color;
      text-align: center;
      font-weight: 400;
      margin-top: 40px; } } }
